//版本通知组件
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue'
export default {
	data() {
		return {
			under: '',
		}
	},
	components: {
		uniNoticeBar
	},
	onShow() {
		this.underway();
	},
	methods: {
		toapplly: function() {
			if (getApp().globalData.userInfo == null) {
				uni.showModal({
					title: '提示',
					content: '请先去授权登陆',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.switchTab({
								url: '/pages/mine/mine'
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
							return false;
						}
					}
				});
			} else {
				uni.navigateTo({
					url: '/pages/apply/apply'
				});
			}

		},
		tonotstart() {
			uni.navigateTo({
				url: '/pages/notstart/notstart'
			});
		},
		show(e){
			console.log(e);
			uni.navigateTo({
			    url: '/pages/show/show?data=' + encodeURIComponent(JSON.stringify(e))
			});
		},
		//进行中的会议
		underway() {
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/underway', //仅为示例，并非真实接口地址。
				method: 'GET',
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 200) {
						_this.under = res.data.data;
					}
				},
				fail: (res) => {
					console.log(res.data);
				}
			});
		}
	}
}
